header:before,
#dropbox:before {
  display: block;
  content: '';
  height: 4px;
  width: 100%;
  background: url("../images/blue_line.jpg");
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.4);
}

/*-------------------------
  Dropbox Element
--------------------------*/

#dropbox {
  background: url("../images/background_tile_3.jpg");
  border-radius: 3px;
  position: relative;
  margin: 20px auto 20px;
  min-height: 290px;
  overflow: hidden;
  padding-bottom: 40px;
  width: 100%;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.3) inset, 0 -3px 2px rgba(0, 0, 0, 0.1);
}

#dropbox .message {
  font-size: 13px;
  text-align: center;
  padding-top: 160px;
  display: block;
  color: #666;
}

#dropbox .message i {
  color: #ccc;
  font-size: 10px;
}

#dropbox:before {
  border-radius: 3px 3px 0 0;
}

/*-------------------------
  Image Previews
--------------------------*/

#dropbox .preview {
  width: 245px;
  height: 215px;
  float: left;
  margin: 55px 0 0 60px;
  position: relative;
  text-align: center;
}

#dropbox .preview img {
  max-width: 240px;
  max-height: 180px;
  border: 3px solid #fff;
  display: block;
  box-shadow: 0 0 2px #000;
}

#dropbox .imageHolder {
  display: inline-block;
  position: relative;
}

#dropbox .uploaded {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: url("../images/done.png") no-repeat center center rgba(255, 255, 255, 0.5);
  display: none;
}

#dropbox .preview.done .uploaded {
  display: block;
}

/*-------------------------
  Progress Bars
--------------------------*/

#dropbox .progressHolder {
  position: absolute;
  background-color: #252f38;
  height: 12px;
  width: 100%;
  left: 0;
  bottom: 0;
  box-shadow: 0 0 2px #000;
}

#dropbox .progress {
  background-color: #2586d0;
  position: absolute;
  height: 100%;
  left: 0;
  width: 0;
  box-shadow: 0 0 1px rgba(255, 255, 255, 0.4) inset;
  -moz-transition: 0.25s;
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
}

#dropbox .preview.done .progress {
  width: 100% !important;
}

